"use client";

import dynamic from "next/dynamic";
import { useState, useEffect } from "react";
import axios from "axios";
import toast, { Toaster } from "react-hot-toast";

function ApprovePageComponent() {
  const [providers, setProviders] = useState([]);
  const [loadingIds, setLoadingIds] = useState([]); // track which provider is loading

  useEffect(() => {
    const fetchPendingProviders = async () => {
      try {
        const res = await axios.get(
          "https://khedme-api.onrender.com/api/providers/pending"
        );
        setProviders(res.data.providers || []);
      } catch (err) {
        console.error("Error fetching providers:", err);
        toast.error("Failed to fetch pending providers ‚ùå");
      }
    };
    fetchPendingProviders();
  }, []);

  const markLoading = (id) => setLoadingIds((s) => [...s, id]);
  const unmarkLoading = (id) => setLoadingIds((s) => s.filter((x) => x !== id));

  const approveProvider = async (id) => {
    markLoading(id);
    try {
      await axios.post(`https://khedme-api.onrender.com/api/providers/approve?id=${id}`);
      toast.success("Provider approved ‚úÖ");
      setProviders((prev) => prev.filter((p) => p.id !== id));
    } catch (err) {
      console.error("Error approving provider:", err);
      toast.error("Approval failed ‚ùå");
    } finally {
      unmarkLoading(id);
    }
  };

  const rejectProvider = async (id) => {
    // optional confirm
    if (!confirm("Reject this provider? This action can be undone by re-registering.")) return;

    markLoading(id);
    try {
      await axios.post(`https://khedme-api.onrender.com/api/providers/reject?id=${id}`);
      toast.success("Provider rejected ‚úÖ");
      setProviders((prev) => prev.filter((p) => p.id !== id));
    } catch (err) {
      console.error("Error rejecting provider:", err);
      toast.error("Reject failed ‚ùå");
    } finally {
      unmarkLoading(id);
    }
  };

  return (
    <div className="p-8 bg-gradient-to-br from-orange-50 to-white text-gray-900 min-h-screen">
      <Toaster position="top-center" />
      <h1 className="text-3xl font-bold mb-6 text-orange-600 text-center">
        Pending Providers
      </h1>

      {providers.length === 0 ? (
        <p className="text-center text-gray-600">No pending providers üéâ</p>
      ) : (
        <div className="max-w-3xl mx-auto grid gap-4">
          {providers.map((p) => (
            <div
              key={p.id}
              className="bg-white shadow-md border border-orange-100 p-5 rounded-2xl flex justify-between items-center hover:shadow-lg transition"
            >
              <div>
                <p className="font-semibold text-lg">{p.name || "Unnamed"}</p>
                <p className="text-gray-600 text-sm">{p.service} ‚Äî {p.district}</p>
              </div>

              <div className="flex items-center gap-3">
                <button
                  onClick={() => rejectProvider(p.id)}
                  disabled={loadingIds.includes(p.id)}
                  className="bg-gray-200 hover:bg-gray-300 text-gray-800 px-3 py-1 rounded-md transition disabled:opacity-50 disabled:cursor-not-allowed"
                >
                  {loadingIds.includes(p.id) ? "Processing..." : "Reject"}
                </button>

                <button
                  onClick={() => approveProvider(p.id)}
                  disabled={loadingIds.includes(p.id)}
                  className="bg-orange-500 hover:bg-orange-600 text-white px-4 py-2 rounded-md transition disabled:opacity-50 disabled:cursor-not-allowed"
                >
                  {loadingIds.includes(p.id) ? "Processing..." : "Approve"}
                </button>
              </div>
            </div>
          ))}
        </div>
      )}
    </div>
  );
}

export default dynamic(() => Promise.resolve(ApprovePageComponent), {
  ssr: false,
});
